// Prevent space scroll
window.addEventListener('keydown', function (e) {
  if (e.keyCode === 32) {
    e.preventDefault();
  }
});
const ENDPOINT = "http://localhost:8080";

let sigmaInstance;
let showEdges = true;
let pickedNodes = [];
let activeState;

$('#generate-btn').click(function () {
  if (sigmaInstance) {
    sigmaInstance.kill();
  }

  showEdges = true;
  $('#show-edges-option').val(showEdges);

  const height = parseInt($('#height-input').val());
  const width = parseInt($('#width-input').val());
  const range = parseInt($('#range-input').val());
  const GRID_HEIGHT = parseInt($('#grid-height-input').val());
  const GRID_WIDTH = parseInt($('#grid-width-input').val());
  const V = parseInt($('#v-input').val());

  if (!height || !width || !range || !V || !GRID_WIDTH || !GRID_HEIGHT) {
    alert("Invalid input");
    return;
  }


  const nodes = [];
  const edges = [];
  const node2coord = {};

  // grid 16 x 16
  let nextId = 0;
  for (let i = 0; i < GRID_HEIGHT; i++) {
    for (let j = 0; j < GRID_WIDTH; j++) {
      const numNodes = Math.floor(V / GRID_HEIGHT / GRID_WIDTH);
      const hfrom = (height / GRID_HEIGHT) * i;
      const wfrom = (width / GRID_WIDTH) * j;


      for (let k = 0; k < numNodes; k++) {
        const x = Math.random() * (width / GRID_WIDTH) + wfrom;
        const y = Math.random() * (height / GRID_HEIGHT) + hfrom;
        node2coord[nextId] = {x, y};
        nodes.push({
          id: nextId,
          label: 'Node ' + nextId,
          x: x,
          y: y,
          size: 1,
          color: '#000'
        });
        nextId++;
      }
    }
  }

  while (nextId < V) {
    const x = Math.random() * width;
    const y = Math.random() * height;
    node2coord[nextId] = {x, y};
    nodes.push({
      id: nextId,
      label: 'Node ' + nextId,
      x: x,
      y: y,
      size: 1,
      color: '#000'
    });
    nextId++;
  }

  // edges
  nextId = 1;
  for (let i = 0; i < V; i++) {
    for (let j = i + 1; j < V; j++) {
      const p1 = node2coord[i];
      const p2 = node2coord[j];
      if ((p1.x - p2.x) * (p1.x - p2.x) + (p1.y - p2.y) * (p1.y - p2.y) <= range * range) {
        edges.push({
          id: 'e' + nextId,
          source: i,
          target: j,
          color: '#ccc'
        });
        nextId++;
      }
    }
  }

  sigmaInstance = new sigma({
    graph: {
      nodes, edges
    },
    settings: {
      borderSize: 2,
      outerBorderSize: 1, // stroke size of active nodes
      minNodeSize: 3,
      maxNodeSize: 4,
      labelThreshold: 10,
      nodeActiveBorderSize: 0,
      nodeActiveOuterBorderSize: 0,
      labelAlignment: 'top',
      defaultNodeBorderColor: '#fff',
      defaultNodeOuterBorderColor: 'rgb(236, 81, 72)', // stroke color of active nodes
      nodeHaloColor: 'rgba(236, 81, 72, 0.2)',
      nodeHaloSize: 5,
    },

    renderer: {
      container: document.getElementById('graph-container'),
      type: 'canvas'
    },
  });
  sigmaInstance.width = width;
  sigmaInstance.height = height;
  activeState = sigma.plugins.activeState(sigmaInstance);

  let keyboard = sigma.plugins.keyboard(sigmaInstance, sigmaInstance.renderers[0]);
  let select = sigma.plugins.select(sigmaInstance, activeState);
  let lasso = new sigma.plugins.lasso(sigmaInstance, sigmaInstance.renderers[0], {
    'strokeStyle': 'rgb(236, 81, 72)',
    'lineWidth': 2,
    'fillWhileDrawing': true,
    'fillStyle': 'rgba(236, 81, 72, 0.2)',
    'cursor': 'crosshair'
  });


  select.bindKeyboard(keyboard);
  select.bindLasso(lasso);


  lasso.bind('selectedNodes', (e) => {
    setTimeout(function () {
      lasso.deactivate();
      sigmaInstance.refresh({skipIdexation: true});
    }, 0);
  });

  sigmaInstance.renderers[0].bind('render', (e) => {
    sigmaInstance.renderers[0].halo({
      nodes: [
        ...activeState.nodes(),
        ...pickedNodes,
      ]
    });
  });


  //"spacebar" + "s" keys pressed binding for the lasso tool
  keyboard.bind('32+83 17+83', () => {
    if (lasso.isActive) {
      lasso.deactivate();
    } else {
      lasso.activate();
    }
  });

  keyboard.bind('27', () => {
    if (lasso.isActive) {
      lasso.deactivate();
    }
  });
  // "spacebar + d" delete active nodes
  keyboard.bind('32+68 17+68', () => {
    activeState.nodes().forEach(node => {
      sigmaInstance.graph.dropNode(node.id);
    });
    sigmaInstance.refresh();
  });


});

$('#show-edges-option').change(function () {
  showEdges = !showEdges;
  if (sigmaInstance) {
    sigmaInstance.settings('drawEdges', showEdges);
    sigmaInstance.refresh();
  }

  $('#show-edges-option').val(showEdges);
});

$('#submit-btn').click(() => {

  const traffic = $('#traffic-input').val();
  if (!traffic || !sigmaInstance) {
    return;
  }
  let edges = [...sigmaInstance.graph.edges()];
  for (let edge of edges) {
    let id = edge.id;
    if (id.substring(0, 2) === 'ex') {
      sigmaInstance.graph.dropEdge(id);
    }
  }

  // network to submit - remapping
  const o2n = {}, n2o = {};
  let nextId = 0;
  for (let node of sigmaInstance.graph.nodes()) {
    o2n[node.id] = nextId;
    n2o[nextId] = node.id;
    nextId++;
  }

  const routingAlgorithm = $('#ra-input').val();
  const pairs = traffic.split('\n').map(str => {
    const regex = /(\d+)->(\d+)/;
    const match = regex.exec(str);
    if (match)
      return {
        source: o2n[parseInt(match[1])],
        destination: o2n[parseInt(match[2])],
        numPacket: 1,
        rate: 1000,
      };
    else {
      return null;
    }
  }).filter(_ => _ !== null);

  pickedNodes = pairs.reduce((acc, {source, destination}) =>
    [...acc,
      sigmaInstance.graph.nodes(n2o[source]),
      sigmaInstance.graph.nodes(n2o[destination]),
    ], []);

  // console.log(sigmaInstance.graph.nodes());
  let nodes = sigmaInstance.graph.nodes().map(node => ({
    id: o2n[node.id],
    x: node.x,
    y: node.y
  }));
  let width = sigmaInstance.width;
  let height = sigmaInstance.height;

  $.ajax({
    url: ENDPOINT + '/exec',
    type: 'POST',
    contentType: 'application/json',
    data: JSON.stringify({
      network: {
        fieldWidth: width,
        fieldHeight: height,
        nodes,
        nodeConfig: { routingAlgorithm }
      },
      simulation: {
        traffic: pairs,
        timeLimit: 1000,
      }
    }),
    success({sessionId}) {
      showResult(sessionId, o2n, n2o);
    }
  })
});

const showResult = (sessionId, o2n, n2o) => {
  function getRandomColor() {
    let samples = [
      "#cc4049",
      "#10cc4b",
      "#1311cc",
      "#06bdcc",
      "#c101cc",
      "#cc0371",
    ];
    return samples[Math.floor(Math.random() * 5)]
  }

  const interval = setInterval(() => {
    $.ajax({
      url: ENDPOINT + `/status/${sessionId}`,
      success({status}) {
        if (status === 'completed') {
          clearInterval(interval);
          $.ajax({
            url: ENDPOINT + `/result/event/${sessionId}`,
            success(data) {
              if (!sigmaInstance) return;
              const events = data.split('\n').filter(_ => _ !== "")
                .map(_ => JSON.parse(_));

              const latest = {};
              const color = {};
              const marked = {};
              for (let event of events) {
                const type = event['e'];
                const packetId = event['p_id'];
                const current = n2o[event['cur']];

                if (type === 'S') {
                  latest[packetId] = current;
                  color[packetId] = getRandomColor();
                } else {
                  if (!marked[[packetId, latest[packetId], current]]) {
                    sigmaInstance.graph.addEdge({
                      id: `ex:${packetId}:${latest[packetId]}:${current}`,
                      source: latest[packetId],
                      target: current,
                      size: 20,
                      color: color[packetId],
                    });
                    marked[[packetId, latest[packetId], current]] = true;
                  }

                  latest[packetId] = current;
                }
              }
              sigmaInstance.refresh();
            }
          })
        }
      }
    })
  }, 400);

};

